  let hoverOn = 0;

  // PlusHOVER.forEach((element, index) => {
  //   element.addEventListener('click', () => {
  //     if (hoverOn === 0) {
  //       PlusTextHOVER[index].style.display = 'block';
  //       hoverOn = 1
  //     }
  //   })
  // })
  // PlusTextHOVER.forEach((element, index) => {
  //   element.addEventListener('mouseleave', () => {
  //     // if (hoverOn === 1) {
  //       if (hoverOn === 1 && !PlusHOVER[index].matches(':hover')) {
  //       PlusTextHOVER[index].style.display = 'none';
  //       hoverOn = 0
  //     }
  //   })
  // })

  let OnPlusTextHOVER = false;
  let timeout;

  PlusHOVER.forEach((element, index) => {
    element.addEventListener("click", () => {
      if (hoverOn === 0) {
        PlusTextHOVER[index].style.display = "block";
        hoverOn = 1;
      }
    });
    element.addEventListener("mouseleave", () => {
      timeout = setTimeout(() => {
        if (!OnPlusTextHOVER) {
          PlusTextHOVER[index].style.display = "none";
          hoverOn = 0;
        }
      }, 500);
    });
  });

  PlusTextHOVER.forEach((element, index) => {
    element.addEventListener("mouseenter", () => {
      OnPlusTextHOVER = true;
      clearTimeout(timeout);
    });
    element.addEventListener("mouseleave", () => {
      OnPlusTextHOVER = false;
      PlusTextHOVER[index].style.display = "none";
      hoverOn = 0;
    });
  });

  const SNS_Arrow = [
    '<i class="fas fa-sort-up"></i>',
    '<i class="fas fa-sort-down"></i>',
  ];

  function SnsSWP() {
    let swiper = new Swiper(".SnsSwiper", {
      direction: "vertical",
      slidesPerView: 1,
      spaceBetween: 30,
      mousewheel: true,
      pagination: {
        el: ".SnsSwiper .swiper-pagination",
        clickable: true,
        bulletClass: "SNS_custom_bullet",
        bulletActiveClass: "swiper-pagination-SNS-custom-bullet",
        renderBullet: function (index, className) {
          return (
            '<div class="' +
            className +
            '"><span>' +
            SNS_Arrow[index] +
            "</span></div>"
          );
        },
      },
    });
  }

  SnsSWP();

